import React from "react";
import Layout from "../components/layout";
import HeaderTwo from "../components/header/header-two";
import { graphql } from 'gatsby'
import Loader from '../template/Card'

const HomeTwo = ({ data }) => {

  const masterList = data.products.edges.map(({ node }, i) => ({ ...node })).map(e => ({
    number: e.num
  }))
  
  return (
    <>  
      <HeaderTwo />
       <Loader 
          pageContext={{
            items: masterList
          }}
       />      
    </>
  );
};

export default HomeTwo;


export const query = graphql`
    {
      products: allMobile {
        edges {
          node {
            id,
            num,
            group
          }
        }
      }
    }
  `
